import { auth as authApi } from "../../../../api";
import actions from "../actions";
import userOperations from "../../operations";

var signUpUserWithEmailAndPassword = function signUpUserWithEmailAndPassword(email, password) {
  return function (dispatch) {
    return authApi.signUpUserWithEmailAndPassword(email, password).then(function (userCredential) {
      return userCredential.user;
    }).then(function (user) {
      if (user && !user.emailVerified) {
        dispatch(userOperations.sendEmailVerification());
      }

      dispatch(actions.signIn(user));
      return user;
    });
  };
};

var signInWithEmailAndPassword = function signInWithEmailAndPassword(email, password) {
  return actions.signIn(authApi.signInWithEmailAndPassword(email, password).then(function (userCredential) {
    return userCredential.user;
  }));
};

export { signUpUserWithEmailAndPassword, signInWithEmailAndPassword };