import types from './types';

var toggleMenu = function toggleMenu() {
  return function (dispatch) {
    return dispatch({
      type: types.TOGGLE_MENU
    });
  };
};

var updateSmUp = function updateSmUp(smUp) {
  return function (dispatch) {
    return dispatch({
      type: types.UPDATE_SMUP,
      payload: {
        smUp: smUp
      }
    });
  };
};

export default {
  toggleMenu: toggleMenu,
  updateSmUp: updateSmUp
};