import { FULFILLED } from "redux-promise-middleware";
import types from "./types";

var add = function add(payload) {
  return {
    type: types.ADD_SHIPLIST,
    payload: payload
  };
};

var remove = function remove(payload) {
  return {
    type: types.REMOVE_SHIPLIST,
    payload: payload
  };
};

var edit = function edit(payload) {
  return {
    type: types.EDIT_SHIPLIST,
    payload: payload
  };
};

var getAll = function getAll(payload) {
  return {
    type: types.GET_SHIPLISTS,
    payload: payload
  };
};

var getAllFulfilled = function getAllFulfilled(payload) {
  return {
    type: types.GET_SHIPLISTS + "_" + FULFILLED,
    payload: payload
  };
};

var addItem = function addItem(meta, payload) {
  return {
    type: types.ADD_SHIPLIST_ITEM,
    meta: meta,
    payload: payload
  };
};

var editItem = function editItem(meta, payload) {
  return {
    type: types.EDIT_SHIPLIST_ITEM,
    meta: meta,
    payload: payload
  };
};

var updateShipListSelected = function updateShipListSelected(shipListIdSelected) {
  return {
    type: types.UPDATE_SHIPLIST_SELECTED,
    shipListIdSelected: shipListIdSelected
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  getAllFulfilled: getAllFulfilled,
  addItem: addItem,
  editItem: editItem,
  updateShipListSelected: updateShipListSelected
};