import { database } from "./firebase";
var databaseUsers = database.ref("/users");

var getDatabaseUser = function getDatabaseUser(uid) {
  return databaseUsers.child(uid);
};

var getDatabaseUserInfo = function getDatabaseUserInfo(uid) {
  return getDatabaseUser(uid).child("info");
};

var getDatabaseUserConfig = function getDatabaseUserConfig(uid) {
  return getDatabaseUser(uid).child("config");
};

var editUserInfo = function editUserInfo(uid, info) {
  return getDatabaseUserInfo(uid).set(info).then(function () {
    return info;
  });
};

var getUserInfo = function getUserInfo(uid) {
  return getDatabaseUserInfo(uid).once("value").then(function (snapshot) {
    return snapshot.val();
  });
};

var editUserConfig = function editUserConfig(uid, config) {
  return getDatabaseUserConfig(uid).set(config).then(function () {
    return config;
  });
};

var getUserConfig = function getUserConfig(uid) {
  return getDatabaseUserConfig(uid).once("value").then(function (snapshot) {
    return snapshot.val();
  });
};

var removeUser = function removeUser(uid) {
  return getDatabaseUser(uid).remove().then(function () {
    return {
      uid: uid,
      removed: true
    };
  });
};

export { getDatabaseUser, editUserInfo, getUserInfo, editUserConfig, getUserConfig, removeUser };