import { FULFILLED, REJECTED } from "redux-promise-middleware";
import types from "./types";

var signUpUserWithEmailAndPassword = function signUpUserWithEmailAndPassword(payload) {
  return {
    type: types.SIGN_UP_USER_WITH_EMAIL_PASSWORD,
    payload: payload
  };
};

var signInWithEmailAndPassword = function signInWithEmailAndPassword(payload) {
  return {
    type: types.SIGN_IN_USER_WITH_EMAIL_PASSWORD,
    payload: payload
  };
};

var signIn = function signIn(payload) {
  return {
    type: types.SIGN_IN,
    payload: payload
  };
};

var signInFulfilled = function signInFulfilled(payload) {
  return {
    type: types.SIGN_IN + "_" + FULFILLED,
    payload: payload
  };
};

var signInRejected = function signInRejected(payload) {
  return {
    type: types.SIGN_IN + "_" + REJECTED,
    payload: payload
  };
};

var signInWithRedirect = function signInWithRedirect(payload) {
  return {
    type: types.SIGN_IN_WITH_REDIRECT,
    payload: payload
  };
};

var signOut = function signOut(payload) {
  return {
    type: types.SIGN_OUT,
    payload: payload
  };
};

var loginInvalidated = function loginInvalidated() {
  return {
    type: types.LOGIN_INVALIDATED
  };
};

export default {
  signUpUserWithEmailAndPassword: signUpUserWithEmailAndPassword,
  signInWithEmailAndPassword: signInWithEmailAndPassword,
  signIn: signIn,
  signInFulfilled: signInFulfilled,
  signInRejected: signInRejected,
  signInWithRedirect: signInWithRedirect,
  signOut: signOut,
  loginInvalidated: loginInvalidated
};