import { category as categoryApi } from "../../api";
import actions from "./actions";

var add = function add(uid, newCategory) {
  return actions.add(categoryApi.add(uid, categoryApi.newId(uid), newCategory));
};

var remove = function remove(uid, id) {
  return actions.remove(categoryApi.remove(uid, id));
};

var edit = function edit(uid, id, updates) {
  return actions.edit(categoryApi.edit(uid, id, updates));
};

var getAll = function getAll(uid) {
  return actions.getAll(categoryApi.getAll(uid));
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges(uid) {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(categories) {
        dispatch(actions.getAllFulfilled(categories));
      };

      return categoryApi.getAll(uid).then(function (categories) {
        listenChangesCallback(categories);
        categoryApi.startListenChanges(uid, listenChangesCallback);
        return categories;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges(uid) {
  return function () {
    if (listenChangesCallback) {
      categoryApi.stopListenChanges(uid, listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};