import { user as userApi, auth as authApi } from "../../api";
import actions from "./actions";

var promiseUserNotLogged = function promiseUserNotLogged() {
  return Promise.reject("User not Logged In");
};

var editUserInfo = function editUserInfo(info) {
  var user = authApi.getCurrentUser();

  if (user) {
    return actions.editUserInfo(userApi.editUserInfo(user.uid, info));
  }

  return promiseUserNotLogged();
};

var getUserInfo = function getUserInfo() {
  var user = authApi.getCurrentUser();
  return actions.getUserInfo(user ? userApi.getUserInfo(user.uid) : null);
};

var editUserConfig = function editUserConfig(config) {
  var user = authApi.getCurrentUser();

  if (user) {
    return actions.editUserConfig(userApi.editUserConfig(user.uid, config));
  }

  return promiseUserNotLogged();
};

var getUserConfig = function getUserConfig() {
  var user = authApi.getCurrentUser();
  return actions.getUserConfig(user ? userApi.getUserConfig(user.uid) : null);
};

var sendEmailVerification = function sendEmailVerification() {
  var user = authApi.getCurrentUser();

  if (user) {
    return actions.sendEmailVerification(user.sendEmailVerification().then(function () {
      return userApi.editUserInfo(user.uid, {
        sentEmailVerification: true
      });
    }));
  }

  return promiseUserNotLogged();
};

export default {
  editUserInfo: editUserInfo,
  getUserInfo: getUserInfo,
  editUserConfig: editUserConfig,
  getUserConfig: getUserConfig,
  sendEmailVerification: sendEmailVerification
};