import { product as productApi, productType as productTypeApi } from "../../api";
import actions from "./actions";

var add = function add(uid, newCategory) {
  return actions.add(productApi.add(uid, productApi.newId(uid), newCategory));
};

var remove = function remove(uid, id) {
  return actions.remove(productApi.remove(uid, id));
};

var edit = function edit(uid, id, updates) {
  return actions.edit(productApi.edit(uid, id, updates));
};

var getAll = function getAll(uid) {
  return actions.getAll(productApi.getAll(uid));
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges(uid) {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(products) {
        dispatch(actions.getAllFulfilled(products));
      };

      return productApi.getAll(uid).then(function (products) {
        listenChangesCallback(products);
        productApi.startListenChanges(uid, listenChangesCallback);
        return products;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges(uid) {
  return function () {
    if (listenChangesCallback) {
      productApi.stopListenChanges(uid, listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};