import { auth as authApi } from "../../../../api";
import actions from "../actions";
import { signUpUserWithEmailAndPassword, signInWithEmailAndPassword } from "./providerEmailPassword";
import { signInGoogleWithPopup, signInGoogleWithRedirect } from "./providerGoogle";
import { startListenAuthChanges, stopListenAuthChanges } from "./authState";
import { startListenUserReloads, stopListenUserReloads } from "./userReloads";

var signByRedirectResult = function signByRedirectResult() {
  return actions.signIn(authApi.getRedirectResult().then(function (userCredential) {
    return userCredential.user;
  }));
};

var signOut = function signOut() {
  return actions.signOut(authApi.signOut());
};

export default {
  signUpUserWithEmailAndPassword: signUpUserWithEmailAndPassword,
  signInWithEmailAndPassword: signInWithEmailAndPassword,
  signInGoogleWithPopup: signInGoogleWithPopup,
  signInGoogleWithRedirect: signInGoogleWithRedirect,
  signByRedirectResult: signByRedirectResult,
  signOut: signOut,
  startListenAuthChanges: startListenAuthChanges,
  stopListenAuthChanges: stopListenAuthChanges,
  startListenUserReloads: startListenUserReloads,
  stopListenUserReloads: stopListenUserReloads
};