import { productType as productTypeApi } from "../../api";
import actions from "./actions";

var add = function add(uid, newProductType) {
  return actions.add(productTypeApi.add(uid, productTypeApi.newId(uid), newProductType));
};

var remove = function remove(uid, id) {
  return actions.remove(productTypeApi.remove(uid, id));
};

var edit = function edit(uid, id, updates) {
  return actions.edit(productTypeApi.edit(uid, id, updates));
};

var getAll = function getAll(uid) {
  return actions.getAll(productTypeApi.getAll(uid));
};

var listenChangesCallback = null;

var startListenChanges = function startListenChanges(uid) {
  return function (dispatch) {
    if (!listenChangesCallback) {
      listenChangesCallback = function listenChangesCallback(productTypes) {
        dispatch(actions.getAllFulfilled(productTypes));
      };

      return productTypeApi.getAll(uid).then(function (productTypes) {
        listenChangesCallback(productTypes);
        productTypeApi.startListenChanges(uid, listenChangesCallback);
        return productTypes;
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenChanges = function stopListenChanges(uid) {
  return function () {
    if (listenChangesCallback) {
      productTypeApi.stopListenChanges(uid, listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  startListenChanges: startListenChanges,
  stopListenChanges: stopListenChanges
};