import { FULFILLED } from "redux-promise-middleware";
import types from "./types";

var add = function add(payload) {
  return {
    type: types.ADD_PRODUCT,
    payload: payload
  };
};

var remove = function remove(payload) {
  return {
    type: types.REMOVE_PRODUCT,
    payload: payload
  };
};

var edit = function edit(payload) {
  return {
    type: types.EDIT_PRODUCT,
    payload: payload
  };
};

var getAll = function getAll(payload) {
  return {
    type: types.GET_PRODUCTS,
    payload: payload
  };
};

var getAllFulfilled = function getAllFulfilled(payload) {
  return {
    type: types.GET_PRODUCTS + "_" + FULFILLED,
    payload: payload
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  getAllFulfilled: getAllFulfilled
};