import { auth } from "../firebase";
import { translateMessageErrorInCatch } from "./translations";
export var signUpUserWithEmailAndPassword = function signUpUserWithEmailAndPassword(email, password) {
  return auth.createUserWithEmailAndPassword(email, password).catch(translateMessageErrorInCatch);
};
export var signInWithEmailAndPassword = function signInWithEmailAndPassword(email, password) {
  return auth.signInWithEmailAndPassword(email, password).catch(translateMessageErrorInCatch);
};
export var sendPasswordResetEmail = function sendPasswordResetEmail(email) {
  return auth.sendPasswordResetEmail(email).catch(translateMessageErrorInCatch);
};
export var updateCurrentUserPassword = function updateCurrentUserPassword(password) {
  return auth.currentUser.updatePassword(password);
};