import { FULFILLED, REJECTED } from "redux-promise-middleware";
import types from "./types";

var add = function add(payload) {
  return {
    type: types.ADD_CATEGORY,
    payload: payload
  };
};

var remove = function remove(payload) {
  return {
    type: types.REMOVE_CATEGORY,
    payload: payload
  };
};

var edit = function edit(payload) {
  return {
    type: types.EDIT_CATEGORY,
    payload: payload
  };
};

var getAll = function getAll(payload) {
  return {
    type: types.GET_CATEGORIES,
    payload: payload
  };
};

var getAllFulfilled = function getAllFulfilled(payload) {
  return {
    type: types.GET_CATEGORIES + "_" + FULFILLED,
    payload: payload
  };
};

var getAllRejected = function getAllRejected(payload) {
  return {
    type: types.GET_CATEGORIES + "_" + REJECTED,
    payload: payload
  };
};

export default {
  add: add,
  remove: remove,
  edit: edit,
  getAll: getAll,
  getAllFulfilled: getAllFulfilled,
  getAllRejected: getAllRejected
};